.button__person {
  align-items: center;
  text-align: center;
}

.button__person button {
  margin: 30px;
  width: 15rem;
  height: 4rem;
  font-size: 20px;
  font-weight: 700;
  outline: none;
  border: none;
  border-radius: 10px;
  box-shadow: inset 0 0 0 0 #001f36;
  background: rgb(16, 84, 116);
  color: rgb(10, 10, 10);
  cursor: pointer;
  transition: ease-out 0.3s;
}

.button__person button:hover {
  border: none;
  box-shadow: inset 300px 0 0 0 #001f36;
  color: rgb(2, 122, 158);
}