.card_person {
  color: var(--clr-neutral-100);
  background-size: cover;
  padding: 10rem 0 0;
  margin-left: 40px;
  margin-bottom: 20px;
  max-width: 45ch;
  border-radius: 0.5rem;
  overflow: hidden;
  transition: transform 500ms ease;
}

#floki {
  background-image: url(/Vikings/static/media/floki.e82828b8.jpg);
}

.card_person:hover,
.card_person:focus-within {
  transform: scale(1.05);
}

.card-content {
  padding: var(--padding);
  height: 320px;
  background: linear-gradient(
    hsl(0 0% 0% / 0),
    hsl(20 0% 0% / 0.3) 20%,
    hsl(0 0% 0% / 1)
  );
}

.card-title {
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 2rem;
}

.card-title::after {
  content: "";
  position: absolute;
  height: 4px;
  left: calc(var(--padding) * -1);
  bottom: -2;
  width: calc(100% + var(--padding));
  background: var(--clr-neutral-400);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 500ms ease;
}

.card_person:hover .card-title::after,
.card_person:focus-within .card-title::after {
  transform: scaleX(1);
}

.card-body-person {
  color: rgb(182, 160, 160);
  font-size: 1.1rem;
}

@media (hover) {
  .card-content {
   transform: translateY(65%);
   transition: transform 500ms ease;
  }
  
  .card_person:hover .card-content, 
  .card_person:focus-within
  .card-content {
    transform: translateY(0);
    transition-delay: 500ms;
  }
  
  .card_person:focus-within .card-content {
    transition-duration: 0ms;
  }
  
  .card-content > *:not(.card-title) {
    opacity: 0;
    transition: opacity 500ms linear;
  }
  
  .card_person:hover .card-content > *:not(.card-title),
  .card_person-focus-within .card-content  > *:not(.card-title) {
    opacity: 1;
    transition-delay: 1000ms;
  }
  
  .card_person:hover .card-content > * {
    opacity: 1;
    transition-delay: 1000ms;
  }

  .card-title::after {
    transform: scaleX(0);
  }
}

